@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
*{box-sizing:border-box}body{background-color:#deeaee}.App{display:flex;justify-content:center;font-family:Roboto}.App-wrapper{display:grid;width:400px}.newComment{padding:20px;margin-bottom:10px;display:flex;flex-direction:column;justify-content:space-between;background-color:#fff;margin-top:20px}.newComment__button{display:grid;margin:0 auto;margin-top:8px;background-color:transparent;border:.5px solid gray;color:gray}.newComment__input{border:1px solid #fff;outline-style:none;background-color:pink;height:40px;padding:10px}.newTitle__input{border:1px solid #fff;outline-style:none;background-color:pink;height:40px;padding:10px}.Comment{background-color:#fff;padding:10px;margin-bottom:10px;font-size:14px;color:#4d4b4b}.Comment__body{margin:10px}.Comment__like-button{width:15px;opacity:50%;margin-top:6px}
